    sort_city() {
      let new_country_info = [];
      if (this.sort_data == "Default") {
        new_country_info = this.reservedCountryInfo;
      } else {
        this.reservedCountryInfo.forEach((stateInfo) => {
          if (stateInfo.city == this.sort_data) {
            new_country_info.push(stateInfo);
          }
        });
      }
      this.countryInfo = new_country_info;
    },
    sortState() {
      let new_country_info = [];
      if (this.sort_data == "Default") {
        new_country_info = this.reservedCountryInfo;
      } else {
        this.reservedCountryInfo.forEach((stateInfo) => {
          if (stateInfo.state == this.sort_data) {
            new_country_info.push(stateInfo);
          }
        });
      }
      this.countryInfo = new_country_info;
