    previewCampaignMedia() {
      let file = this.campaignInfo.campaignMedia;
      this.file = this.campaignInfo.campaignMedia;
      let fileType = file.type;
      if (
        fileType == "video/mp4" ||
        fileType == "video/webm" ||
        fileType == "video/ogg"
      ) {
        this.preview_campaign_media = true;
        this.previewMedia = URL.createObjectURL(this.file);
      }
    },
    validateFile() {
      this.errors = [];
      let file = this.campaignInfo.campaignMedia;
      //console.log(file);
      let fileSize = (file.size / (1024 * 1024)).toFixed(2);
      let fileType = file.type;
      let f_name = file.name;
      if (fileSize <= 50) {
        if (
          fileType == "video/mp4" ||
          fileType == "video/webm" ||
          fileType == "image/jpeg" ||
          fileType == "image/jpg" ||
          fileType == "image/png" ||
          fileType == "image/gif" ||
          fileType == "video/ogg"
        ) {
          this.campaignInfo.mediaType = fileType;
          this.campaignInfo.file_name = f_name;
          this.campaignInfo.fSize = fileSize;
        } else {
          this.errors.push({
            value: "INVALID FILE TYPE! ONLY :MP4,WEBM,JPG,PNG AND GIF ALLOWED",
          });
        }
      } else {
        this.errors.push({ value: "FILE TOO LARGE, MAX FILESIZE = 50MB" });
      }
      this.handleErrors();
    },
