        this.currentScreen.population <= 20000000
      ) {
        k = k + 2;
      } else if (
        this.currentScreen.gdp > 20000 &&
        this.currentScreen.population >= 20000001
      ) {
        k = k + 2.5;
      }
      this.campaignInfo.multiplyConstant =
        this.campaignInfo.multiplyConstant +
        parseInt(this.currentScreen.price_per_day) * k;
      //console.log(this.campaignInfo.numberOfDays);
    },
    selectAllDisplays() {
      if (this.checkbox == true) {
        //check all checkboxes
        for (let p = 0; p < this.checkboxStates.length; p++) {
          this.checkboxStates[p] = 1;
        }
        //set screens  to zero
        this.campaignInfo.numberOfScreens = 0;
        //increase number of screens
        this.campaignInfo.numberOfScreens = this.countryInfo.length;
        this.campaignInfo.campaignDisplays = this.countryInfo;
        //set price to zero
        this.campaignInfo.capaignCost = 0;
        this.countryInfo.forEach((screenInfo) => {
          let lat = screenInfo.latitude;
          let lng = screenInfo.longitude;
          let coords = { lat: parseFloat(lat), lng: parseFloat(lng) };
          this.map_coordinates.push(coords);
          //set current screen
          this.currentScreen = screenInfo;
          //call method to calculate price for current screen
          this.price_calculator();
        });
      } else {
        for (let p = 0; p < this.checkboxStates.length; p++) {
          this.checkboxStates[p] = 0;
        }
        //set screens  to zero
        this.campaignInfo.numberOfScreens = 0;
        //set price to zero
        this.campaignInfo.capaignCost = 0;
        this.campaignInfo.multiplyConstant = 0;
        this.map_coordinates = [];
      }
    },
    resetScreens() {
      if (this.checkbox == false) {
        //set screens  to zero
        this.campaignInfo.numberOfScreens = 0;
        //set price to zero
        this.campaignInfo.capaignCost = 0;
        this.map_coordinates = [];
      }
    },
    setScreens(object_index) {
      if (this.checkboxStates[object_index] == true) {
        // this.checkMe=1;
        this.campaignInfo.numberOfScreens++;
        this.campaignInfo.campaignDisplays.push(this.countryInfo[object_index]);
        let lat = this.countryInfo[object_index].latitude;
        let lng = this.countryInfo[object_index].longitude;
        let coords = { lat: parseFloat(lat), lng: parseFloat(lng) };
        this.map_coordinates.push(coords);
        //set current screen
        this.currentScreen = this.countryInfo[object_index];
        //call method to calculate price for current screen
        this.price_calculator();
      } else {
        this.campaignInfo.numberOfScreens =
          this.campaignInfo.numberOfScreens - 1;
        this.campaignInfo.campaignDisplays.pop(this.countryInfo[object_index]);
        let lat = this.countryInfo[object_index].latitude;
        let lng = this.countryInfo[object_index].longitude;
        let coords = { lat: parseFloat(lat), lng: parseFloat(lng) };
        this.map_coordinates.pop(coords);
        //set current screen
        this.currentScreen = this.countryInfo[object_index];
        //call method to calculate price for current screen
        this.reduce_price();
      }
    },
    createCurrencySymbol(country) {
      let currency = "";
      //set currency symbol
      if (country == "Nigeria") {
        currency = "₦";
      } else if (country == "Cameroon") {
        currency = "XAF";
      }
      return currency;
    },
    getScreens() {
      console.log("get screens called");
      //set currency symbol
      if (this.campaignInfo.campaignCountry == "Nigeria") {
        this.currency = "₦";
      } else if (this.campaignInfo.campaignCountry == "Cameroon") {
        this.currency = "XAF";
      }
    },
    setScreenView(url){
      this.currentScreen=url;
      this.screenView=true;
    }
  },
  data: () => ({
    depositeAmount:0,
    showDepositBox:false,
    currentScreen:'',
    screenView:false,
    campaign_upload_result: "",
    showLoader: true,
    loaderWidth: 0,
    UploadDialogue: false,
    currentCampaign: {},
    deleteModal: false,
    e1: 1,
    file: null,
    preview_campaign_media: false,
    previewMedia: "",
    mapKey: 1,
    archivedCamp: 5,
    reload_map_component: false,
    checkbox: false,
    errorModal: false,
    errors: [],
    modalText: {
      headerText: "Error Message",
      actionText: "",
      questionText: "",
    },
    dialog: false,
    countryInfo: ["nigeria 300"],
    reservedCountryInfo: [],
    activeCampaign: true,
    archivedCampaign: true,
    cities: ["Hamburg", "Berlin", "Cologne"],
    states: [],
    sort_data: "",
    district: ["Lorem ipsum", "Lorem ipsum1", "Lorem ipsum2"],
    map_coordinates: [], //{lat:6.453056,lng:3.395833},{lat:6.50153,lng:3.35808},{lat:6.4281,lng:3.4219}
    currentScreen: {}, //current screen selected
    more: false,
    map: false,
    slot: "color:red",
    items: [
      "Aim 10% of the playing time of all selected displays",
      "Aim 20% of the playing time of all selected displays",
      "Aim 30% of the playing time of all selected displays",
      "Aim 40% of the playing time of all selected displays",
    ],
    checkboxStates: [],
    countries: ["Cameroon"],
    currency: "",
    campaignFileError: 0,
    campaignInfo: {
      campaignSubmit: 1,
      campaignName: "",
      numberOfDays: 5,
      campaignCountry: "",
      campaignDisplays: [],
      campaignMedia:"",
      campaignCapacity: "",
      campaignCost: 0,
      numberOfScreens: 0,
      multiplyConstant: 0,
      startDate: "",
      endDate: "",
      mediaType: "",
      currency: "",
      file_name: "",
      duration: 0,
      fSize: 0,
    },
  }),

  computed: mapGetters([
    "activeCampaigns",
    "archivedCampaigns",
    "unApprovedCampaigns",
    "not_paid_campaigns",
    "dateRangeText",
    "userDetails",
    "return_date_array",
    "screensInfo",
    "screenCities",
    "screenStates",
    "screen_checkboxStates",
    "wallet"
  ]),
  created() {
    
  },
  mounted() {
    //add zito pay url to script
     let script = document.createElement('script');
      script.setAttribute('src', 'https://zitopay.africa/assets/js/payment.js?v=0007');
      document.head.appendChild(script);
    let data = this.screensInfo;
    this.countryInfo = data;
    this.reservedCountryInfo = data;
    this.cities = this.screenCities;
    this.states = this.screenStates;
    this.checkboxStates = this.screen_checkboxStates;
  },
};
</script>
